<template>
    <div>
        <label class="switch">
            <input type="checkbox" v-model="checked" @change="$emit('toggle-switch', purpose)">
            <span class="slider round"></span>
        </label>
    </div>
</template>

<script>
    export default {
        name: "SwitchElement",

        props: {
            startValue: Boolean,
            purpose: String
        },

        data() {
            return {
                checked: this.startValue
            }
        }
    }
</script>

<style scoped>
    .switch {
        position: relative;
        display: inline-block;
        width: 60px;
        height: 34px;
    }

    .switch input {
        opacity: 0;
        width: 0;
        height: 0;
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: grey;
        -webkit-transition: .4s;
        transition: .4s;
    }

    .slider:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: #212121;
        -webkit-transition: .4s;
        transition: .4s;
    }

    input:checked + .slider {
        background-color: #617d89;
    }

    input:focus + .slider {
        box-shadow: 0 0 1px #212121;
    }

    input:checked + .slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
    }

    /* Rounded sliders */
    .slider.round {
        border-radius: 34px;
    }

    .slider.round:before {
        border-radius: 50%;
    }
</style>